import { ReactElement } from "react";

import ButtonBase from "@mui/material/ButtonBase";

import Paragraph from "./Paragraph";

export default function HomeInfo() {
  return (
    <div style={{ paddingTop: 24, paddingBottom: 24 }}>
      <div style={{ textAlign: "center", fontWeight: "bold", fontSize: 20 }}>
        BEAUTIFUL HOME FOR <b>SALE</b> IN <u>CANAAN HEIGHTS, WV</u>
        <br />
        JUST MINUTES FROM CANAAN VALLEY AND THE TOWN OF DAVIS!
      </div>
      <br />
      <Paragraph
        color="#363550"
        borderColor="#3e83d1"
        titleColor="#4f4e76"
        shadowColor="#29283d"
        align="left"
        title="Location"
        text={
          <span>
            This home sits on 3.5 acres on Canaan Loop Road just off Route 32
            atop Canaan Mountain, midway between Canaan Valley and the town of
            Davis, in Tucker County, West Virginia.
          </span>
        }
      ></Paragraph>
      <br />
      <Paragraph
        color="#253637"
        borderColor="#22896e"
        titleColor="#3d5a5c"
        shadowColor="#1b2728"
        align="right"
        title="Interior"
        text={
          <span>
            The first floor shows a cozy entry room for greeting guests with
            space for easy chairs and a glass-front propane stove to warm the
            room. A beautifully appointed kitchen has space for a table that
            seats four with a large walk-in pantry nearby. Off the kitchen is
            the laundry room/half bath with washer and dryer and two sinks.
            <br />
            <br />
            The centerpiece of the downstairs is a great room with beamed
            ceiling, a working stone fireplace, and a spacious floor-to-ceiling
            built-in bookshelf. The upstairs features a roomy master bedroom
            with full bath, a TV loft with two work/computer desks plus a guest
            bedroom at the other end of the house. Theres a balcony with stairs
            to the ground off the guest room. Flooring in all major rooms is
            hardwood or wide pine plank throughout with tongue-and-grove cedar
            and pine walls and ceilings.
            <br />
            <br />A wide covered porch wraps around three sides of the house. A{" "}
            <b>7000W</b> propane-fired generator supports selected electrical
            circuits in the event of a power outage. Two-inch foam insulates the
            crawl space walls with a vapor barrier on walls and floor.
          </span>
        }
      ></Paragraph>
      <br />
      <Paragraph
        color="#363550"
        borderColor="#3e83d1"
        titleColor="#4f4e76"
        shadowColor="#29283d"
        align="left"
        title="Barn"
        text={
          <span>
            Outside, theres a two-story barn with a roomy workshop on the first
            floor and a large storage room filling the entire second floor. A
            substantial amount of workshop hardware and lumber materials
            conveys. A horse stable is attached to the barn. Follow the brick
            walk from the homes ample covered porch to a large{" "}
            <u>18x18 gazebo</u> and beyond that to the <u>14x24 garage</u>{" "}
            with electric opener.
          </span>
        }
      ></Paragraph>
      <br />
      <Paragraph
        color="#253637"
        borderColor="#22896e"
        titleColor="#3d5a5c"
        shadowColor="#1b2728"
        align="right"
        title="Driveway"
        text={
          <span>
            The property has two entrance driveways, one from Route 32 and one
            from paved <b>Canaan Loop Road</b>, the latter sweeping through the
            wooded entranceway to a parking circle next to the home. A lovely
            pond graces the lower end of the property, filled with bluegills
            ready to give any angler some fun.
          </span>
        }
      ></Paragraph>
      <br />
      <Paragraph
        color="#363550"
        borderColor="#3e83d1"
        titleColor="#4f4e76"
        shadowColor="#29283d"
        align="left"
        title="Town & Conveniences"
        text={
          <span>
            The entire property is just five minutes via Route 32 to the town of
            Davis with its restaurants, grocery store, bank, post office,
            24-hour convenience store/gas station, churches and nearby
            Blackwater Falls State Park. In the other direction on Route 32 is
            Canaan Valleys two downhill skiing destinations of Timberline and
            Canaan Valley State Park and the nationally famous White Grass
            cross-country skiing center.
          </span>
        }
      ></Paragraph>
      <br />
      <Paragraph
        color="#372525"
        borderColor="#892222"
        titleColor="#5d3e3e"
        shadowColor="#241818"
        title="Price & Contact"
        text={
          <span>
            This property is for sale by owner at <b>$475,000</b>. In the event
            an agreement is reached with a buyer, closing date will be set for{" "}
            <u>after July 1, 2023</u>. In the event multiple bids are submitted
            for this property, the high bid must be at least <b>$5,000</b>{" "}
            higher than a prior bid in order to kick it out.
            <br />
            <br />
            For more information contact the owner at 304-314-4337 or e-mail at{" "}
            <a href="mailto:canaanhome4sale@gmail.com?subject=Canaan Home Inquiry">
              canaanhome4sale@gmail.com
            </a>
            .
          </span>
        }
      ></Paragraph>
    </div>
  );
}
