import Lightbox, { ImagesListType } from "react-spring-lightbox";

import React from "react";

import Modal from "@mui/material/Modal";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";

import { styled } from "@mui/material/styles";
import ButtonBase from "@mui/material/ButtonBase";

import { isMobile } from "react-device-detect";

import IconButton from "@mui/material/IconButton";
import SkipNext from "@mui/icons-material/SkipNext";
import SkipPrevious from "@mui/icons-material/SkipPrevious";
import Close from "@mui/icons-material/Close";

import Paragraph from "./Paragraph";

const ImageButton = styled(ButtonBase)(({ theme }) => ({
  position: "relative",
  height: isMobile ? 50 : 200,
  [theme.breakpoints.down("sm")]: {
    width: isMobile ? "30% !important" : "90% !important", // Overrides inline-style
    height: isMobile ? 100 : 100,
  },
  "&:hover, &.Mui-focusVisible": {
    zIndex: 1,
    "& .MuiImageBackdrop-root": {
      opacity: 0.15,
    },
    "& .MuiImageMarked-root": {
      opacity: 0,
    },
    "& .MuiTypography-root": {
      border: "4px solid currentColor",
    },
  },
}));

const ImageSrc = styled("span")({
  position: "absolute",
  left: 0,
  right: 0,
  top: 0,
  bottom: 0,
  backgroundSize: "cover",
  backgroundPosition: "center 40%",
  borderRadius: 12,
});

export default function Images() {
  const [open, setOpen] = React.useState(false);

  const makeClose = () => {
    setOpen(false);
  };

  const images: ImagesListType = [];
  const imageButtons: any[] = [];
  for (let i = 1; i <= 23; i++) {
    const path = "images/DSC_00" + i.toString().padStart(2, "0") + ".jpg";
    images.push({ src: path, loading: "lazy", alt: "Pic" });

    imageButtons.push(
      <ImageButton
        focusRipple
        key="Pic"
        onClick={() => handleOpen(i - 1)}
        style={{
          width: isMobile ? 50 : 200,
          margin: 20,
        }}
        className="myImageButton"
      >
        <ImageSrc style={{ backgroundImage: `url(${path})` }} />
      </ImageButton>
    );
    /*<Button onClick={() => handleOpen(0)}>ONE</Button>*/
  }

  const [currentImageIndex, setCurrentIndex] = React.useState(0);

  const gotoPrevious = () =>
    currentImageIndex > 0 && setCurrentIndex(currentImageIndex - 1);

  const gotoNext = () =>
    currentImageIndex + 1 < images.length &&
    setCurrentIndex(currentImageIndex + 1);

  const handleOpen = (i: number) => {
    setOpen(true);
    setCurrentIndex(i);
  };

  return (
    <div style={{ marginBottom: 200, marginTop: 30 }}>
      <Paragraph
        color="#363550"
        borderColor="#3e83d1"
        titleColor="#4f4e76"
        shadowColor="#29283d"
        title="Pictures of the Home"
        text={
          <span style={{ padding: 0, margin: 0 }}>
            Click on a picture to view it. While viewing, swipe to view the
            other pictures or double click to zoom in.
          </span>
        }
      ></Paragraph>
      {imageButtons}
      <CoolLightbox
        isOpen={open}
        initIndex={currentImageIndex}
        onClose={makeClose}
        gotoPrevious={gotoPrevious}
        gotoNext={gotoNext}
        images={images}
      />
    </div>
  );
}

const CoolLightbox = (props: {
  isOpen: boolean;
  initIndex: number;
  onClose: any;
  gotoPrevious: any;
  gotoNext: any;
  images: ImagesListType;
}) => {
  //const [currentOpen, setCurrentOpen] = React.useState(props.isOpen);

  return (
    <Lightbox
      isOpen={props.isOpen}
      onPrev={props.gotoPrevious}
      onNext={props.gotoNext}
      images={props.images}
      currentIndex={props.initIndex}
      // Add your own UI
      // renderHeader={() => (<CustomHeader />)}
      renderFooter={() => (
        <div style={{ backgroundColor: "white", display: "flex", height: 60 }}>
          <div style={{ margin: "auto" }}>
            <IconButton
              aria-label="delete"
              size="large"
              onClick={() => props.gotoPrevious()}
            >
              <SkipPrevious fontSize="inherit" />
            </IconButton>
            <IconButton
              aria-label="delete"
              size="large"
              onClick={() => props.onClose()}
            >
              <Close fontSize="inherit" />
            </IconButton>
            <IconButton
              aria-label="delete"
              size="large"
              onClick={() => props.gotoNext()}
            >
              <SkipNext fontSize="inherit" />
            </IconButton>
          </div>
        </div>
      )}
      // renderPrevButton={() => <Button color="secondary">Secondary</Button>}
      // renderNextButton={() => <Button color="secondary">Secondary</Button>}
      // renderImageOverlay={() => (
      //   <div
      //     style={{ width: "100%", height: "100%", backgroundColor: "black" }}
      //   ></div>
      // )}

      // Add styling
      // className="cool-class"
      style={{
        backgroundColor: isMobile ? "rgba(0, 0, 0, 0.8)" : "rgba(0, 0, 0, 0.4)",
        zIndex: 10_000,
      }}
      // Handle closing
      onClose={props.onClose}

      // Use single or double click to zoom
      // singleClickToZoom

      // react-spring config for open/close animation
      // pageTransitionConfig={{
      //   from: { transform: "scale(0.75)", opacity: 0 },
      //   enter: { transform: "scale(1)", opacity: 1 },
      //   leave: { transform: "scale(0.75)", opacity: 0 },
      //   config: { mass: 1, tension: 320, friction: 32 }
      // }}
    />
  );
};

