import * as React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
//import TabPanel from "@mui/material/Tab";
import HomeIcon from "@mui/icons-material/Home";
import ImageIcon from "@mui/icons-material/Image";
import ContactMailIcon from "@mui/icons-material/ContactMail";

import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import AppBar from "@mui/material/AppBar";

import Container from "@mui/material/Container";

import HomeInfo from "./HomeInfo";
import Images from "./Images";
import ContactUs from "./ContactUs";

import { isMobile } from "react-device-detect";

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 0, m: 0 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

export default function MyTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    const obj = document.getElementById("scrollContainer");
    if (obj) obj.scrollTop = 0;
    setValue(newValue);
  };

  const textColor = "#cac4d0";
  const selectedColor = "#42bc7f";

  const tc = (i: number) => (value === i ? selectedColor : textColor);
  const iconMargin = (i: number) => {
    const result = {
      marginTop: 0,
      height: 20,
      marginBottom: 12,
      transition:
        "margin 0.2s ease-out, transform 0.5s cubic-bezier(0,2.38,1,1.83)",
      transform: "scale(1.4)",
    };
    if (value !== i) {
      result.marginTop = 14;
      result.marginBottom = 6;
      result.transform = "scale(1)";
    }
    return result;
  };

  const transition = "color 0.2s ease-out";

  // let touchstartX = 0;
  // let touchendX = 0;

  // function checkDirection() {
  //   if (touchendX - touchstartX < -30) {
  //     if (value < 2) setValue(value + 1);
  //   }
  //   if (touchendX - touchstartX > 30) {
  //     if (value > 0) setValue(value - 1);
  //   }
  // }

  // function onTouchStart(e: any) {
  //   touchstartX = e.changedTouches[0].screenX;
  // }

  // function onTouchEnd(e: any) {
  //   touchendX = e.changedTouches[0].screenX;
  //   checkDirection();
  //   touchstartX = 0;
  //   touchendX = 0;
  // }

  return (
    <Box
      sx={{
        width: "100%",
        height: "100vh",
        display: "flex",
        flexDirection: "column",
      }}
    >
      <AppBar
        sx={{
          borderBottom: 1,
          borderColor: "divider",
          bgcolor: "#1c1b1f",
          position: isMobile ? "sticky" : "static",
        }}
      >
        <div
          style={{
            marginTop: 8,
            marginBottom: 0,
            fontSize: 32,
            fontFamily: "Anton",
          }}
        >
          Canaan Home for <span style={{ color: "#bf4e4e" }}>Sale</span>!
          <br />
          <div
            style={{
              marginBottom: 4,
              marginTop: 0,
              fontSize: 24,
              fontFamily: "Caveat",
            }}
          >
            <i>(A wonderful West Virginia home is available now!)</i>
          </div>
        </div>
        <Tabs
          variant="fullWidth"
          value={value}
          onChange={handleChange}
          aria-label="icon label tabs example"
          allowScrollButtonsMobile
          sx={{ color: "white" }}
          TabIndicatorProps={{ style: { background: selectedColor } }}
        >
          <Tab
            style={{ color: tc(0), transition }}
            icon={<HomeIcon style={iconMargin(0)} />}
            label="Home Info"
          />
          <Tab
            style={{ color: tc(1), transition }}
            icon={<ImageIcon style={iconMargin(1)} />}
            label="Pictures"
          />
          <Tab
            style={{ color: tc(2), transition }}
            icon={<ContactMailIcon style={iconMargin(2)} />}
            label="Contact Us!"
          />
        </Tabs>
      </AppBar>
      <div
        style={{
          display: "flex",
          height: "calc(100vh - 170px)",
          padding: 0,
          margin: 0,
          backgroundColor: "#181d29",
          backgroundImage:
            "radial-gradient(rgba(255, 255, 255, 0.2) 4%, transparent 6%), radial-gradient(rgba(255, 255, 255, 0.2) 4%, transparent 7%)",
          backgroundSize: "15px 15px",
          backgroundRepeat: "repeat",
          // backgroundImage: 'url("images/background4.jpg")',
          // backgroundRepeat: "repeat",
          // boxShadow: "inset 0 0 0 2000px rgba(0, 0, 0, 0.5)",
          // backgroundSize: "960px 540px",
        }}
      >
        <Container
          style={{
            height: "100%",
            overflow: "auto",
            flexGrow: 1,
            margin: 0,
            padding: 0,
          }}
          id="scrollContainer"
          // onTouchStart={onTouchStart}
          // onTouchEnd={onTouchEnd}
        >
          <TabPanel value={value} index={0}>
            <div
              style={
                isMobile
                  ? {
                      paddingTop: "30px",
                      paddingBottom: "60px",
                    }
                  : {}
              }
            >
              <HomeInfo />
            </div>
          </TabPanel>
          <TabPanel value={value} index={1}>
            <Images />
          </TabPanel>
          <TabPanel value={value} index={2}>
            <ContactUs />
          </TabPanel>
        </Container>
      </div>
    </Box>
  );
}
