import Image from "./Image";

interface ParagraphData {
  text: any;
  title: string;
  align?: string;
  borderColor: string;
  color: string;
  titleColor: string;
  shadowColor: string;
}

export default function Paragraph(data: ParagraphData) {
  //data.borderColor = "#ffffff";
  const borderColor = "rgba(255, 255, 255, 0.5)";
  const border = "1px solid " + borderColor;

  const isRight = data.align === "right";
  const isLeft = data.align === "left";

  const style: React.CSSProperties = {
    textAlign: "left",
    fontSize: 18,
    borderRadius: isRight
      ? "12px 0px 0px 12px"
      : isLeft
      ? "0px 12px 12px 0px"
      : "12px 12px 12px 12px",
    borderRight: border,
    borderTop: border,
    borderBottom: border,
    borderLeft: border,
    //backgroundColor: data.color,
    background: `linear-gradient(180deg, ${data.titleColor} 0px, ${data.color} 45px, ${borderColor} 45px, ${borderColor} 46px, ${data.shadowColor} 46px, ${data.color} 70px, ${data.color} 100%)`,

    padding: 14,
    paddingTop: 6,
    paddingBottom: 8,
    paddingLeft: 14,
    paddingRight: 14,

    boxShadow: "0px 0px 30px rgba(0, 0, 0, 0.4)",
  };

  const textStyle: React.CSSProperties = {};

  if (isRight) {
    style.borderRight = undefined;
    style.marginLeft = 30;
    textStyle.float = "right";
  } else if (isLeft) {
    style.borderLeft = undefined;
    style.marginRight = 30;
    textStyle.float = "left";
  } else {
    style.marginLeft = 30;
    style.marginRight = 30;
  }

  style.display = "flex";

  const showImages = false; //window.innerWidth > 600;

  const img = <Image url="images/DSC_0001.jpg" />;

  return (
    <div style={style}>
      {showImages && isRight ? img : null}
      <div>
        <div
          style={{
            paddingTop: 0,
            fontWeight: "normal",
            fontSize: 24,
          }}
        >
          {data.title}
        </div>
        <div style={{ paddingBottom: 8 }}></div>
        <div style={textStyle}>
          {Array.isArray(data.text)
            ? data.text.map(function (item: any, index: number) {
                return (
                  <span key={index}>
                    {item}
                    <br />
                    <br />
                  </span>
                );
              })
            : data.text}
        </div>
      </div>
      {showImages && isLeft ? img : null}
    </div>
  );
}
